/* Syntax highlighting */
.highlight {
  margin: 0;
  padding: 0.5rem 10px;
  border: 1px solid #e1e1e1;
  background: #f7f7f7;
  border-radius: 2px;
}
.highlight {
  position: relative;
}
.highlight:before {
  position: absolute;
  top: 0.25rem;
  left: -40px;
  font-weight: bold;
  width: 25px;
  text-align: left;
  color: #303f9f;
  font-family: monospace, serif;
  font-weight: 400;
}
// .highlight-none .highlight:before {
//   content: 'Out';
//   color: #d84315;
//   top: 0rem;
// }
.highlight-none .highlight {
  background: #ffffff;
  border: 0;
  padding: 0;
  margin: 0rem 0 1.5rem 0;
}
.highlight pre {
  overflow-x: auto;
  white-space: pre;
  word-wrap: normal;
  margin: 0.25rem 0;
}
